
import { defineComponent } from 'vue';

import AtomBackdrop from '@/components/atoms/AtomBackdrop.vue';

export default defineComponent({
	name: 'MoleculeModal',
	components: {
		AtomBackdrop,
	},
	props: {
		isShow: {
			type: Boolean,
			required: true,
			default: false,
		},
	},
	emits: ['closeModal'],
	setup(props, { emit }) {
		const closeModal = () => {
			emit('closeModal');
		};

		return { closeModal };
	},
});
